
.login-head h1{
  background: #61c3db;
  height: 4.3rem;
  text-align: center;
  font: 1.6rem/4.3rem '微软雅黑';
  color: #fff;
}
.login-log{
  width: 29.8rem;
  height: 3.8rem;
  border: 0.1rem solid #c9cacb;
  font: 1.6rem/3.8rem '微软雅黑';
  text-align: center;
  color: #333;
  margin: 3rem auto 0;
  border-radius: 0.5rem;
}
.input{
  width: 28.9rem;
  margin: 6rem auto 0;
  height: 8.6rem;
}
.input input{
  border: none;
  width: 100%;
  padding: 0;
  margin: 0;
  height: 4.3rem;
  box-sizing: border-box;
  border-bottom: 1px solid #d0d0d0;
  background: none;
  font: 1.6rem/4.3rem '微软雅黑';
}
.login-tip{
  width: 20rem;
  margin: 2rem auto;
  background: #40e3e3;
  font: 1.4rem/2.4rem '微软雅黑';
  color: #fff;
  text-align: center;
  border-radius: 1rem;
}
.outlogin{
  position: absolute;
  bottom: 10rem;
  background: #fff;
  font: 1.5rem/4.5rem '微软雅黑';
  color: red;
  text-align: center;
  width: 100%;
}



