
.fill{
  stroke-dasharray:275;
  stroke-dashoffset:278;
  -webkit-animation: dash 1s infinite linear;
          animation: dash 1s infinite linear;
}
@-webkit-keyframes dash {
to {
    stroke-dashoffset: 0;
}
}
@keyframes dash {
to {
    stroke-dashoffset: 0;
}
}
#svg{
  display: block;
  width: 3rem;
  height: 3rem;
  margin: 0 auto;
}
.search-main{
  /*height: 10rem;
  background: red;*/
}
.search-warp{
  position: relative;
  z-index: 999;
  height: 3rem;
  padding: 0.65rem 0 0.65rem 1rem;
  background: #61c3db;
}
.search-warp input{
  display: inline-block;
  width: 22.8rem;
  height: 3rem;
  border-radius: 1.3rem;
  font: 1.3rem/3rem '微软雅黑';
  color: #333;
  text-indent: 2.8rem;
}
.search-warp span{
  display: inline-block;
  font: 1.6rem/3rem '微软雅黑';
  color: #fff;
}
.search-content{
  position: absolute;
  left: 0;
  right: 0;
  top: 4.3rem;
  bottom: 0;
  height: auto;
}
.search-list ul{
  padding-left: 1rem;
}
.search-list li{
  height: 5.45rem;
  border-bottom: 1px solid #c8c8c8;
}
.song-name{
  font: 1.6rem/2.3rem '微软雅黑';
  margin-top: 0.7rem;
  color: #507daf;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
.song-info{
  font: 1.2rem/1.9rem '微软雅黑';
  margin-top: 0.2rem;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
.singer-name, .song-info i{
  color: #a5a6a8;
}
.album-name{
  color: ##507daf;
}



