
.footer{
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 4.9rem;
  background: #333;
  z-index: 999;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
.foot-menu{
  width: 25%;
  height: 100%;
  font-size: 1.4rem;
  line-height: 1.7rem;
  color: #a7a7a7;
}
.foot-menu span{
  display: block;
  text-align: center;
}
.foot-icon{
  height: 2.5rem;
}
.foot-text{
  height: 1.7rem;
}



