
.up{
  margin-top: 8.25rem;
}
.up-swiper{
  position: relative;
}
.up-pagnination{
  position: absolute;
  width: 100%;
  text-align: center;
  bottom: .8rem;
  left: 0;
  z-index: 10;
}
.up-pagnination .swiper-pagination-bullet{
  width: 0.6rem;
  height: 0.6rem;
  margin: 0 0.2rem;
}
.up-swiper-img{
  width: 100%;
  height: 12.4rem;
}
.up-swiper-img img{
  width: 100%;
  height: 100%;
}
.up-content{
  margin-top: 1rem;
}
.up-content-head{
  border-bottom: 1px solid #b9baba;
}
.up-content-title{
  font: 1.6rem/4.8rem '微软雅黑';
}
.up-list li{
  height: 7rem;
  border-bottom: 1px solid #b9baba;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
.up-list-img{
  width: 7rem;
  height: 7rem;
}
.up-list-img img{
  width: 100%;
  height: 100%;
}
.up-list-text{
  width: 22.2rem;
  margin-left: 1rem;
}
.up-list-text p{
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.up-text-floor1{
  font: 1.5rem/4.5rem '微软雅黑';
  color: #323233;
}
.up-text-floor2{
  font: 1.1rem/1.3rem '微软雅黑';
  color: #7d7d7f;
}



