
.type1{
  padding: 0 1rem;
}
.content-head{
  padding-top: 1rem;
  height: 3.6rem;
}
.sub-title{
  font: 1.6rem/3.6rem '微软雅黑';
}
.type1-list{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
.type1-list li{
  width: 9.5rem;
  height: 14.9rem;
}
.type1-img{
  display: block;
  height: 9.5rem;
}
.type1-img img{
  width: 100%;
  height: 100%;
}
.type1-text{
  width: 100%;
  height: 3rem;
  font: 1.2rem/1.5rem '微软雅黑';
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  text-overflow: ellipsis;
  -webkit-box-orient: vertical;
  margin-top: 0.7rem;
}



