
body, html{
  height: 100%;
  overflow: hidden;
}
.page-head{
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 999;
}
.page-search{
  height: 3rem;
  padding: 0.65rem 0;
  background: #61c3db;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  border-bottom: 1px solid #57afc5;
}
.icon-mc, .icon-player{
  width: 5.4rem;
  height: 100%;
}
.page-search .search{
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  height: 3rem;
  padding: 0;
  margin: 0;
  border: none;
  border-radius: 0.6rem;
  font: 1.4rem/3rem 'Avenir', Helvetica, Arial, sans-serif;
  text-indent: 1.8rem;
  background: #fff;
  color: #999;
}
.head-menu{
  height: 3.95rem;
  background: #f9f9f9;
  border-bottom: 1px solid #dfdfe0;
  padding: 0 0.45rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
.head-menu .head-sub{
  width: 7rem;
  text-align: center;
  position: relative;
  font: 1.4rem/3.95rem '微软雅黑';
  color: #333;
}
.head-menu .head-line{
  position: absolute;
  left: 0;
  bottom: -1px;
  width: 100%;
  height: 0.2rem;
  background: #5fc2db;
  display: none;
}



