
.swiper{
  position: relative;
  /*margin-top: 8.26rem;*/
}
.auto-img{
  height: 11.8rem;
}
.sub-img{
  width: 100%;
  height: 11.8rem;
}
.swiper-pagination{
  position: absolute;
  left: 0;
  bottom: 0.6rem;
  width: 100%;
  text-align: center;
}
.swiper-pagination .swiper-pagination-bullet{
  margin: 0 0.2rem;
  height: 0.4rem;
  width: 0.4rem;
}



