  position: absolute;
  width: 100%;
  top: 8.25rem;
}*/
.music-menu-head{
  padding: 1.4rem 1rem 0;
  height: 11rem;
  background-image: linear-gradient(
    to left,
    rgba(157, 157, 157, 0.8) 0%,
    rgba(140, 132, 112, 0.8) 33.3%,
    rgba(145, 136, 116, 0.8) 66.6%,
    rgba(153, 153, 153, 0.8) 100%
    );
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
.mm-head-img{
  width: 10rem;
  height: 10rem;
}
.mm-head-img img{
  width: 100%;
  height: 100%;
}
.mm-head-text{
  width: 19rem;
}
.mm-head-text h1{
  font: 1.9rem/3.4rem '微软雅黑';
  color: #fff;
  font-weight: normal;
}
.mm-head-text h2{
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  font: 1.5rem/3rem '微软雅黑';
  color: #fff;
}
.mm-head-text p{
  font: 1.1rem/1.4rem '微软雅黑';
  color: #d2c4b7;
}
.mm-option{
  padding: 2rem 0;
  height: 3rem;
}
.mm-option-title{
  float: left;
  width: 10.4rem;
  height: 2.8rem;
  border: 0.1rem solid #cbcccd;
  border-radius: 1.4rem;
  text-align: center;
  margin-left: 1rem;
}
.mm-option-title span{
  display: inline-block;
  font: 1.4rem/2.8rem '微软雅黑';
  color: #323233;
}
.mm-option-title i{
  display: inline-block;
  font-style: normal;
  font: 1.4rem/2.8rem 'MWF-MDL2';
  color: #cbcccd;
  margin-left: 0.5rem;
}
.mm-optin-content{
  float: right;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.mm-optin-content span{
  font: 1.4rem/3rem '微软雅黑';
  color: #646566;
  padding: 0 1rem;
}
.mm-optin-content i{
  height: 1.4rem;
  width: 1px;
  background: #ccc;
}
.mm-list{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.mm-list li{
  width: 15.8rem;
  height: 21.6rem;
}
.mm-list-img{
  display: block;
  height: 15.8rem;
}
.mm-list-img img{
  width: 100%;
  height: 100%;
}



