
.select{
  padding: 0 1rem;
}
.select-list{
  border-top: 1px solid #e2e3e3;
}
.select-list li{
  height: 6.9rem;
  border-bottom: 1px solid #e2e3e3;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.select-img{
  width: 15.5rem;
  height: 100%;
}
.select-img img{
  width: 100%;
  height: 100%;
}
.select-text{
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.select-text-floor1{
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  text-overflow: ellipsis;
  -webkit-box-orient: vertical;
  vertical-align: middle;
  height: 4.2rem;
}
.select-text-floor1 i{
  padding: 0 0.2rem;
  display: inline-block;
  font: 0.8rem/1.2rem '微软雅黑';
  border: 1px solid #fe5253;
  color: #fe5253;
  font-style: normal;
  vertical-align: middle;
}
.select-text-floor1 span{
  font: 1.2rem/2.1rem '微软雅黑';
  color: #323232;
}
.select-text-floor2{
  font: 1.2rem/2rem '微软雅黑';
  color: #a1a1a2;
  margin-top: 0.2rem;
}



