
.play{
  height: 100%;
  background-image:linear-gradient(135deg, #040403 0%, #232e3a 40%, #263f60 60%, #363b46 90%, #5b5c64 100%);
}
.play-header{
  height: 4.5rem;
  box-sizing: border-box;
  border-bottom: 1px solid #313231;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.play-icon{
  width: 6.3rem;
  font: 1.5rem/4.5rem '微软雅黑';
  text-align: center;
  color: #fff;
}
.play-title{
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  color: #fff;
  overflow: hidden;
  text-align: center;
  margin-top: 0.5rem;
}
.play-songName{
  font: 1.5rem/2.1rem '微软雅黑';
  white-space: nowrap;
}
.play-albumName{
  font: 1.2rem/2.1rem '微软雅黑';
  white-space: nowrap;
}
.play-record{
  height: 32.5rem;
  position: relative;
  overflow: hidden;
}
.play-img{
  width: 30rem;
  height: 30rem;
  margin: 4.5rem auto 0;
}
.play-img img, .play-pointer img{
  width: 100%;
  height: 100%;
}
.play-pointer{
  position: absolute;
  top: 0;
  left: 14.5rem;
  width: 8rem;
  height: 11.9rem;
  -webkit-transform-origin: 1.65rem 0;
          transform-origin: 1.65rem 0;
  -webkit-transform: rotate(0);
          transform: rotate(0);
}
.pausestatus{
  -webkit-animation: 1s playpointer forwards;
          animation: 1s playpointer forwards;
}
.playstatus{
  -webkit-animation: 1s pausepointer forwards;
          animation: 1s pausepointer forwards;
}
.play-option{
  height: 7.6rem;
  overflow: hidden;
  position: relative;
}
.play-option ul{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  transition: all .5s linear;
}
.play-option li{
  text-align: center;
  font: 1.2rem/2.4rem '微软雅黑';
  color: #fff;
  opacity: .4;
}
.play-option .active{
  opacity: 1;
}
.play-timer{
  height: 1.6rem;
  padding: 0 1.2rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  font: 1rem/1.6rem '微软雅黑';
  color: #d5d8dc;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
.timer-line{
  width: 21.2rem;
  height: 0.2rem;
  background: #3a4758;
  margin-top: 0.7rem;
  position: relative;
  z-index: 15;
}
.current-icon{
  position: absolute;
  width: 1.6rem;
  height: 1.6rem;
  border-radius: 0.8rem;
  background: #fff;
  top: -0.7rem;
  right: -0.8rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  z-index: 20;
}
.current-icon span{
  width: 0.4rem;
  height: 0.4rem;
  border-radius: 0.2rem;
  background: red;
}
.play-btns{
  padding-top: 2rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.play-sub{
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  font: 2rem/4.5rem '微软雅黑';
  color: #fff;
}
.icon-wrap{
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  z-index: 10;
}
@-webkit-keyframes playpointer {
from {
      -webkit-transform: rotate(0);
              transform: rotate(0);
}
to {
      -webkit-transform: rotate(-25deg);
              transform: rotate(-25deg);
}
}
@keyframes playpointer {
from {
      -webkit-transform: rotate(0);
              transform: rotate(0);
}
to {
      -webkit-transform: rotate(-25deg);
              transform: rotate(-25deg);
}
}
@-webkit-keyframes pausepointer {
from {
      -webkit-transform: rotate(-25deg);
              transform: rotate(-25deg);
}
to {
      -webkit-transform: rotate(0);
              transform: rotate(0);
}
}
@keyframes pausepointer {
from {
      -webkit-transform: rotate(-25deg);
              transform: rotate(-25deg);
}
to {
      -webkit-transform: rotate(0);
              transform: rotate(0);
}
}



